import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { ActivatedRoute, } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import * as moment from 'moment';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalService } from 'ngx-bootstrap/modal';
import DataSource from 'devextreme/data/data_source';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { AuthService, Helper, Library, VChanggeMoneyService, VStringService } from '@core/index';
import { pattern } from '@config/index';
import { VatService } from '@modules/vat/shared';
import { AddCustomerComponent } from '@shared/index';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import { sEnum } from '@config/sEnum';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ListsService } from '@modules/lists/shared';
import { ChooseHdckComponent } from '../choose-hdck/choose-hdck.component';
import { FormExtendComponent } from '@shared/components/form-extend/form-extend.component';
import * as _ from 'lodash';
import { InvoiceService } from '@shared/service/invoice.service';

@Component({
  selector: 'vat-form-invoice',
  templateUrl: './form-invoice.component.html',
  styleUrls: ['./form-invoice.component.scss']
})
export class FormInvoiceComponent implements OnInit, OnDestroy, AfterViewInit {
  routeSub: Subscription;
  @ViewChildren(DxFormComponent) listDxForm: any;
  @ViewChild(DxFormComponent) form: DxFormComponent;
  @ViewChild(FormExtendComponent) formExtend: FormExtendComponent;
  @Input() frmData: any;
  @Input() type: any;
  @Input() dataSource: any;
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() _objTemplateSelect: any;
  @Input() _oTemplateTT78: any;
  @Input() extentionGrid: any;
  @Input() isShowButtonWaiteSign: boolean;
  @Input() isTT78: boolean;
  @Input() pkInvoiceSelectWaitSign: any;
  @Input() infoDangKy: any;
  @Input() isRequired: boolean;
  @Input() ttDaXuatHoaDon: boolean = false;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Output('onInvoiceCurrent') onInvoiceCurrent = new EventEmitter<any>();
  @Output('onTemplateChange') onTemplateChange = new EventEmitter<any>();
  @Output('onTemplateTypeChange') onTemplateTypeChange = new EventEmitter<any>();
  @Output('onTemplateTT78TypeChange') onTemplateTT78TypeChange = new EventEmitter<any>();

  originInvoiceNumber: any;
  bsModalRef: BsModalRef;
  bsModalHdckRef: BsModalRef;
  template_temp: any;
  select: any = {};
  isShowToKhai = false;
  pattern = pattern;
  tax_codes: any = [];
  customer_codes: any = [];
  readOnlyCacTruongConLai: boolean;
  showTraLai = false;
  disableInvoiceDate = this.authService.isAllowEditInvoiceDate();
  htXuatOptions = [];
  showPopup = false;
  customerCurent: any = {};
  emails: any = [];
  configs: any = {};
  private arrEmail: any = [];
  listRoleBranchOrigin: Array<any>;
  role_branch: boolean;
  isShowBanGiaoXe: boolean;
  isShowBaoDuongXe: boolean;
  // thay i disable ca 3 input customer_name, buyer_name, buyer_address
  tod: any = {};
  _translate: any;
  formItems = [];
  tax_code_search: any;
  customForms = [];
  customerTempt = {};
  dataTaxCode: any;
  template_type: any;
  httpOptions = {
    headers: new HttpHeaders({
      'Authorization': `Bearer ${JSON.parse(localStorage.getItem('access_token')).value}`,
      'TaxCode': JSON.parse(localStorage['currentUser']).account.tax_code,
      'Content-Type': 'application/json',
    })
  };
  mst_btn_options = {
    text: '', icon: 'fa fa-plus-circle', type: 'default',
    onClick: () => {
      const initialState = {
        isVisibleGhiVaThemMoi: false
      };
      this.bsModalRef = this.modalService.show(AddCustomerComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      const submodal = this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveData) {
          const customer = this.bsModalRef.content.successSaveData;
          this.frmData.customer_name = customer.name;
          this.frmData.buyer_tax_code = customer.tax_code;
          this.frmData.buyer_name = customer.contact_person_name;
          this.frmData.buyer_address = customer.business_address;
          this.frmData.customer_code = customer.code;
          this.frmData.buyer_mobile = customer.contact_mobile;

          if (customer.email) {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
            const stringDataEmailGet = customer.email.split(';');
            for (let i = 0; i < stringDataEmailGet.length; i++) {
              this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
            }
          } else {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
          }
          if (customer.bank_account_number) {
            this.frmData.bank_account_number = customer.bank_account_number;
          }
          if (customer.bank_name) {
            this.frmData.bank_name = customer.bank_name;
          }
        }
        submodal.unsubscribe();
      });
    }
  };

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private vatService: VatService,
    public translate: TranslateService,
    private customersService: CustomersService,
    private http: HttpClient,
    private listService: ListsService,
    private invoiceService: InvoiceService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngAfterViewInit() {
    this.formDuLieuMoRong(this.template_temp);
    if(this.frmData.total_payment){
      const tien_chu = Helper.tienBangChu(Math.abs(this.frmData.total_payment), VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), this.frmData.currency_code, this.configs.system_parameter.display_read_money_english);
      this.frmData.total_payment_in_word = this.invoiceService.docTienAm(this.frmData.total_payment, tien_chu, this.configs, this.frmData.currency_code);
    }
  }

  ngOnInit() {
    this.htXuatOptions = VSelectBoxService._htXuatOptions(this._translate, this.frmData.status);

    this.role_branch = this.listService.getDataConfigLocalStorage();
    if (this.route.snapshot.queryParams['status'] === 'return') {
      this.showTraLai = true;
      this.frmData.nguoitralai = this.authService.getCurrentUser().user.name;
      this.frmData.ngaytralai = moment(new Date()).format('YYYY-MM-DD');
    }

    this.routeSub = this.route.data.subscribe((data: any) => {
      this.configs = this.authService.getConfigs();

      // put data DM for select
      let danh_muc = this.authService.getDMLocal();
      this.select.unit_codes = danh_muc.DM_DON_VI_TINH ? danh_muc.DM_DON_VI_TINH.data : null;
      this.select.payment_method_names = danh_muc.DM_HINH_THUC_THANH_TOAN ? danh_muc.DM_HINH_THUC_THANH_TOAN.data : null;
      this.select.currency_codes = danh_muc.DM_LOAI_TIEN ? danh_muc.DM_LOAI_TIEN.data : null;
      this.select.customer_types = danh_muc.DM_NHOM_KHACH_HANG ? danh_muc.DM_NHOM_KHACH_HANG.data : null;
      this.select.export_places = danh_muc.DM_NOI_XUAT_HOA_DON ? danh_muc.DM_NOI_XUAT_HOA_DON.data : null;
      this.select.list_bank_account_seller = danh_muc.DM_TAI_KHOAN_NGAN_HANG ? danh_muc.DM_TAI_KHOAN_NGAN_HANG.data : null;
      this.select.list_bank_account_seller.forEach(item => {
        item.name_display = item.code + ': ' + item.name;
      });
      if (this.isTT78) {
        let temptTT78 = [];
        if (!this.frmData.id) {
          temptTT78 = data.init[1];
        }
        else {
          temptTT78 = data.init[3];
        }
        if (temptTT78 && temptTT78.length > 0) {
          this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.infoDangKy.is_invoice_with_code);
          if (Number.parseInt(this.frmData.invoice_number) > 0) {
            if (this.frmData.is_invoice_with_code != this.infoDangKy.is_invoice_with_code) {
              this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.frmData.is_invoice_with_code);
            }
          }
        }
        if (this.frmData.id)
          this.changeTemplateInvoice(this.frmData.invoice_date);
      }
      else {
        this.select.invoice_releases = data.init[0];
        if (!this.frmData.id) {
          this.select.invoice_releases = this.select.invoice_releases.filter(p => p.number_survival > 0 && p.release_status != 'DA_HUY_SO');
        }
      }
    });
    this.select.customer_object_codes = VSelectBoxService._customerObject(this._translate);
    this.select.adjustment_forms = VSelectBoxService._adjustmentForms(this._translate);
    this.select.adjustment_objects = VSelectBoxService._adjustmentObjects(this._translate);

    // Set disabled cho n v t chc
    this.setDisabledByCustomer_object_code(this.frmData.customer_object_code);

    // tm kim m s thu
    this.tax_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'tax_code');
      }
    });

    // // tm kim m khch hng
    this.customer_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'code');
      }
    });
    if (!this.isTT78) {
      this.select.invoice_releases.forEach(item => {
        if (item.template_id === this.frmData.template_id) {
          if (item.template_type == sEnum.templatetype.HoaDonBanGiaoXe)
            this.isShowBanGiaoXe = true;
          else
            this.isShowBanGiaoXe = false;
          if (item.template_type == sEnum.templatetype.HoaDonBaoDuongXe)
            this.isShowBaoDuongXe = true;
          else
            this.isShowBaoDuongXe = false;
          // Nu l mu html
          if (item.template_type == sEnum.templatetype.HoaDonKiemToKhaiHoanVe)
            this.isShowToKhai = true;
          else
            this.isShowToKhai = false;
          this.template_temp = item;
        }
      });
    }
    else {
      this.select.templatesTT78.forEach(item => {
        if (item.value === this.frmData.template_id) {
          this.frmData.invoice_release_id = "";
          if (item.template_type == sEnum.templatetype.HoaDonBanGiaoXe)
            this.isShowBanGiaoXe = true;
          else
            this.isShowBanGiaoXe = false;
          if (item.template_type == sEnum.templatetype.HoaDonBaoDuongXe)
            this.isShowBaoDuongXe = true;
          else
            this.isShowBaoDuongXe = false;
          // Nu l mu html
          if (item.template_type == sEnum.templatetype.HoaDonKiemToKhaiHoanVe)
            this.isShowToKhai = true;
          else
            this.isShowToKhai = false;
          this.template_temp = item;
        }
      });
    }
    if (this.frmData.buyer_email) {
      this.emails = [];
      const emailToString = this.frmData.buyer_email;
      const a11 = emailToString.replace(/;/g, ',');
      this.emails = a11.split(',');
    }
  }

  formDuLieuMoRong(item) {
    this.customForms = [];
    this.customerTempt = {};
    Helper.resetObject(this.formItems);
    if (item.fields && item.fields.length > 0) {
      const _arrayGrid = item.fields.filter(x => x.type === '5');
      if (_arrayGrid && _arrayGrid.length > 0) {
        this.extentionGrid.ma_hang = false;
        this.extentionGrid.so_lo = false;
        this.extentionGrid.han_dung = false;
        this.extentionGrid.nuoc_san_xuat = false;
        this.extentionGrid.don_hang_hop_dong = false;
        this.extentionGrid.ma_quan_ly = false;
        for (let i = 0; i < _arrayGrid.length; i++) {
          const column = _arrayGrid[i]['name'];
          const value = _arrayGrid[i]['is_show'];
          const _ma = VStringService._convertToViKdau(column);
          this.extentionGrid[VStringService._convertToViKdau(column)] = value;
        }
      }
      let _dulieumorong = _.cloneDeep(item.fields).filter(x => x.type === '4');
      const invoice_options = this.frmData.invoice_options;

      if (invoice_options.length > 0) {
        _dulieumorong = _dulieumorong.map((dl) => {
          let option = invoice_options.find(x => x.code == dl.code)
          if (option) {
            dl.value = option.value;
          }
          return dl;
        });
      }

      this.frmData.invoice_options = _dulieumorong;
      if (_dulieumorong.length > 0) {
        this.formExtend.createForm(this.customForms, _dulieumorong)
      }
      _dulieumorong.forEach(value => {
        this.customerTempt[value.code] = value.value;
      });
    }
    else {
      this.frmData.invoice_options = [];
      this.extentionGrid.ma_hang = false;
      this.extentionGrid.so_lo = false;
      this.extentionGrid.han_dung = false;
      this.extentionGrid.nuoc_san_xuat = false;
      this.extentionGrid.don_hang_hop_dong = false;
      this.extentionGrid.ma_quan_ly = false;
    }
  }

  // createForm() {
  //   this.form.instance.beginUpdate();
  //   this.form.instance.itemOption('business_addressa', { label: { text: 'ia chaaa' }, editorOptions: { placeholder: 'Nhp a ch' } });
  //   this.form.instance.endUpdate();
  // }

  async editTaxCode() {
    this.listRoleBranchOrigin = [];
    if (this.role_branch) {
      const roleUser = await this.listService.getDataUserLocalStorege();
      if (roleUser) {
        this.listRoleBranchOrigin = await this.handleListRoleBranch(roleUser);
      } else {
        this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
      }
      if (Array.isArray(this.listRoleBranchOrigin) && this.listRoleBranchOrigin.length) {
        if (this.listRoleBranchOrigin.length === 2) {
          this.listRoleBranchOrigin = this.listRoleBranchOrigin.filter(value => {
            if (value.code !== 'TAT_CA') {
              value.checked = true;
              return value;
            }
          });
        }
      }
    }
    const initialState = {
      isVisibleGhiVaThemMoi: false,
      role_branch: this.role_branch,
      roleBranchStatus: this.listRoleBranchOrigin
    };
    this.bsModalRef = this.modalService.show(AddCustomerComponent, {
      class: 'modal-lg full-modal', initialState, backdrop: 'static',
      keyboard: false
    });
    const submodal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveData) {
        const customer = this.bsModalRef.content.successSaveData;
        this.frmData.customer_name = customer.name;
        this.frmData.buyer_tax_code = customer.tax_code;
        this.frmData.buyer_name = customer.contact_person_name;
        this.frmData.buyer_address = customer.business_address;
        this.frmData.customer_code = customer.code;
        this.frmData.buyer_mobile = customer.contact_mobile;

        if (customer.email) {
          this.frmData.buyer_email = customer.email;
          this.emails = [];
          const stringDataEmailGet = customer.email.split(';');
          for (let i = 0; i < stringDataEmailGet.length; i++) {
            this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
          }
        } else {
          this.frmData.buyer_email = customer.email;
          this.emails = [];
        }
        if (customer.bank_account_number) {
          this.frmData.bank_account_number = customer.bank_account_number;
        }
        if (customer.bank_name) {
          this.frmData.bank_name = customer.bank_name;
        }
      }
      submodal.unsubscribe();
    });
  }

  ////// To form m rng
  createCustomGroup() {
    return {
      itemType: 'group',
      colCount: '2',
      items: this.customForms
    };
  }

  updateCustomerInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.code) !== VStringService._getValueToString(this.frmData.customer_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };

  updateTaxcodeInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.tax_code) !== VStringService._getValueToString(this.frmData.buyer_tax_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };

  /////// s kin thay i cc trng m rng
  changeItem(_dulieumorong) {
    _dulieumorong.forEach(item => {
      for (let key in this.customerTempt) {
        if (item.code === key) {
          item.value = this.customerTempt[key];
        }
      }
      this.frmData.invoice_options = _dulieumorong;
    });
  }

  async handleListRoleBranch(roleUser) {
    let arrList: any;
    arrList = await this.listService.handleGetDMtoCustomer(roleUser);
    if (Array.isArray(arrList) && arrList.length) {
      return this.listService.handleCheckAll(arrList);
    }
    return null;
  }

  disableTemplateInput() {
    if (this.frmData.invoice_number) {
      return true;
    }
    return false;
  }

  readonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = true;
    });
    this.readOnlyCacTruongConLai = true;
  }

  public removeReadonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = false;
    });
    this.readOnlyCacTruongConLai = false;
  }

  // reset trng m rng
  public resetTMR() {
    this.formExtend.resetForm();
    this.formExtend.resetOptions();
  }

  setDisabledByCustomer_object_code(value) {
    this.tod.customer_name = false;
    this.tod.buyer_name = false;
    this.tod.buyer_address = false;
    switch (value) {
      case 'DOANH_NGHIEP':
        break;
      case 'CA_NHAN':
        this.frmData.customer_name = '';
        this.tod.customer_name = true;
        break;
      case 'KHACH_LE':
        this.frmData.customer_name = '';
        this.frmData.buyer_address = '';
        this.tod.customer_name = true;
        this.tod.buyer_address = true;
        break;
      default:
        break;
    }
  }

  // gi data ln parent component
  onFieldDataChanged() {
    this.formEvent.emit(this.frmData);
  }

  getHtXuatValue() {
    return this.frmData.status;
  }

  getFrmData() {
    return this.frmData;
  }

  // s kin
  taxCodeKeyUp = (e) => {
    const str = this.frmData.buyer_tax_code;
    const len = str.length;
    if (len >= 3) {
      this.vatService.searchMST(str).subscribe(rs => {
        if (rs.status === 'success') {
          const arr = [];
          rs.data.customers.forEach(item => {
            arr.push(item);
          });
          this.tax_codes = arr;
        }
      });
    }
  };

  taxCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.customer_code = customer.code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    this.frmData.bank_account_number = customer.bank_account_number ? customer.bank_account_number : '';
    this.frmData.bank_name = customer.bank_name ? customer.bank_name : '';
    ////
    this.onFieldDataChanged();
  }

  customerCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.buyer_tax_code = customer.tax_code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    this.frmData.bank_account_number = customer.bank_account_number ? customer.bank_account_number : '';
    this.frmData.bank_name = customer.bank_name ? customer.bank_name : '';
    this.onFieldDataChanged();
  }

  customerChanged = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.name) !== VStringService._getValueToString(this.frmData.customer_name)
              || VStringService._getValueToString(this.customerCurent.contact_person_name) !== VStringService._getValueToString(this.frmData.buyer_name)
              || VStringService._getValueToString(this.customerCurent.contact_mobile) !== VStringService._getValueToString(this.frmData.buyer_mobile)
              || VStringService._getValueToString(this.customerCurent.business_address) !== VStringService._getValueToString(this.frmData.buyer_address)
              || VStringService._getValueToString(this.customerCurent.email) !== VStringService._getValueToString(this.frmData.buyer_email)
              || VStringService._getValueToString(this.customerCurent.bank_name) !== VStringService._getValueToString(this.frmData.bank_name)
              || VStringService._getValueToString(this.customerCurent.bank_account_number) !== VStringService._getValueToString(this.frmData.bank_account_number)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };
  customer_object_codeChange = (e) => {
    this.setDisabledByCustomer_object_code(e.value);
  };

  templateChange = (e) => {
    this.select.invoice_releases.forEach(item => {
      if (e.value === item.value) {
        this.frmData.template_id = item.template_id;
        this.onTemplateChange.emit(item.is_multi_vat);
        this.onTemplateTypeChange.emit(item);
        if (item.template_type == sEnum.templatetype.HoaDonBanGiaoXe)
          this.isShowBanGiaoXe = true;
        else
          this.isShowBanGiaoXe = false;
        if (item.template_type == sEnum.templatetype.HoaDonBaoDuongXe)
          this.isShowBaoDuongXe = true;
        else
          if (item.template_type == sEnum.templatetype.HoaDonKiemToKhaiHoanVe)
            this.isShowToKhai = true;
          else
            this.isShowToKhai = false;
        this.isShowBaoDuongXe = false;
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            this._objTemplateSelect[key] = value;
          }
        }
        if (!this._objTemplateSelect.is_discount) {
          this.invoice_products.load().then((dataSource) => {
            dataSource.forEach((item, index) => {
              item.discount = null;
              item.amount_discount = null;
            });
          });
          VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._objTemplateSelect, this.configs.system_parameter);
          if (VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBay
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBayThuTien
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBayHoanTien
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonXangDau) {
            VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._objTemplateSelect.template_type));
          }
        }

        // Nu l mu html
        this.formDuLieuMoRong(item);
      }
    });
  };

  onChangeTem = (e) => {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.frmData.invoice_release_id = "";
        this.frmData.template_id = item.value;
        this.frmData.invoice_series = item.invoice_series;
        this.frmData.template_code = item.template_code;
        this.onTemplateChange.emit(item.is_multi_vat);
        this.onTemplateTT78TypeChange.emit(item);
        if (item.template_type == sEnum.templatetype.HoaDonBanGiaoXe)
          this.isShowBanGiaoXe = true;
        else
          this.isShowBanGiaoXe = false;
        if (item.template_type == sEnum.templatetype.HoaDonBaoDuongXe)
          this.isShowBaoDuongXe = true;
        else
          this.isShowBaoDuongXe = false;
        if (item.template_type == sEnum.templatetype.HoaDonKiemToKhaiHoanVe)
          this.isShowToKhai = true;
        else
          this.isShowToKhai = false;
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            this._oTemplateTT78[key] = value;
          }
        }
        if (!this._oTemplateTT78.is_discount) {
          this.invoice_products.load().then((dataSource) => {
            dataSource.forEach((item, index) => {
              item.discount = null;
              item.amount_discount = null;
            });
          });
          VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._oTemplateTT78, this.configs.system_parameter);
          if (VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBay
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBayThuTien
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBayHoanTien
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonXangDau) {
            VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._oTemplateTT78.template_type));
          }
        }

        // Nu l mu html
        this.formDuLieuMoRong(item);
      }
    });
  };


  currencyCodeChange = (e) => {
    if (this.frmData.currency_code === 'VND') {
      this.frmData.currency_rate = null;
    }
    const tien_chu = Helper.tienBangChu(Math.abs(this.frmData.total_payment), VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), this.frmData.currency_code, this.configs.system_parameter.display_read_money_english);
    this.frmData.total_payment_in_word = this.invoiceService.docTienAm(this.frmData.total_payment, tien_chu, this.configs, this.frmData.currency_code);
  };
  currencyRateChange = (e) => {
    if (!this.isTT78)
      VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._objTemplateSelect, this.configs.system_parameter);
    else
      VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._oTemplateTT78, this.configs.system_parameter);
  };

  exportPlaceChange = (e) => {
    const _obj = this.select.export_places.filter(p => p.code === e.value)[0];
    if (_obj) {
      this.frmData.export_address = _obj.dia_chi;
      this.frmData.export_place_name = _obj.name;
    }
  };
  bankAccountSellerChange = (e) => {
    const _obj = this.select.list_bank_account_seller.filter(p => p.code === e.value)[0];
    if (_obj) {
      this.frmData.seller_bank_account_name = _obj.name;
      // this.frmData.export_place_name = _obj.name;
    } else {
      this.frmData.seller_bank_account_name = '';
    }
  };

  paymentMethodChange = (e) => {
    const _obj = this.select.payment_method_names.filter(p => p.code === e.value)[0];
    if (_obj) {
      this.frmData.payment_method_view_name = _obj.name;
    }
  };

  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }

  capNhatDmKhachHang() {
    this.customerCurent.tax_code = VStringService._getValueToString(this.frmData.buyer_tax_code);
    this.customerCurent.name = VStringService._getValueToString(this.frmData.customer_name);
    this.customerCurent.code = VStringService._getValueToString(this.frmData.customer_code);
    this.customerCurent.contact_person_name = VStringService._getValueToString(this.frmData.buyer_name);
    this.customerCurent.contact_mobile = VStringService._getValueToString(this.frmData.buyer_mobile);
    this.customerCurent.business_address = VStringService._getValueToString(this.frmData.buyer_address);
    this.customerCurent.email = VStringService._getValueToString(this.frmData.buyer_email);
    this.customerCurent.bank_name = VStringService._getValueToString(this.frmData.bank_name);
    this.customerCurent.bank_account_number = VStringService._getValueToString(this.frmData.bank_account_number);
    const data = { customer: this.customerCurent };
    this.customersService.edit(data, this.customerCurent.id).subscribe((response: any) => {
      if (response.status === 'success') {
        this.showPopup = false;
      } else {
        Library.notify(response.message, 'error');
      }
    }, err => {
    }, () => {
    });
  }

  huy() {
    this.showPopup = false;
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.customerChanged(null);
    this.formEvent.emit(this.frmData);
  }
  /* Chn ha n ch k */
  showModalHdck() {
    let release = this.select.invoice_releases.filter(p => p.template_id == this.frmData.template_id)[0];
    if (release) {
      let invoiceReleaseId = release.value;
      const initialState = {
        invoiceNumberOrigin: "0",
        invoiceReleaseId: invoiceReleaseId,
        onSave: (data) => {
          if (VStringService._getValueToString(this.pkInvoiceSelectWaitSign) != "") {
            this.onInvoiceCurrent.emit(this.pkInvoiceSelectWaitSign);
            this.frmData.status = sEnum.statusInv.ChoKy;
          }
          this.frmData.id = data.id;
          this.frmData.invoice_date = data.invoice_date;
          this.frmData.invoice_number = data.invoice_number;
        }
      };
      this.bsModalHdckRef = this.modalService.show(ChooseHdckComponent, {
        backdrop: 'static',
        keyboard: false,
        class: 'modal-lg full-modal',
        initialState
      });
    }
  }

  changeDateTime = (e) => {
    this.changeTemplateInvoice(e.value);
  }

  changeTemplateInvoice(strDate) {
    if (this.isTT78) {
      let year = "";
      if (VStringService._getValueToString(strDate) != "")
        year = VStringService._getValueToString(strDate).substring(4, 2);
      else {
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        year = VStringService._getValueToString(invoiceDate).substring(4, 2);
      }
      let templates = this.select.templatesTT78;
      this.select.templatesTT78 = [];
      if (templates && templates.length > 0) {
        templates.forEach(item => {
          if (VStringService._getValueToString(item.name) != "")
            item.name = item.name.replace(item.name.substring(4, 2), year);
          // for (const key in item) {
          //   if (item.hasOwnProperty(key)) {
          //     const value = item[key];
          //     this._oTemplateTT78[key] = value;
          //   }
          // }
          if (item.value === this.frmData.template_id) {
            this.onTemplateTT78TypeChange.emit(item);
            //  item.invoice_series = item.invoice_series.replace(item.invoice_series.substring(3, 1), year);
            // this.frmData.invoice_series = item.invoice_series;
            // this.frmData.template_code = item.template_code;
          }
          this.select.templatesTT78.push(item);
        });
      }
    }
  }
  changeTaxCode(e) {
    this.frmData.buyer_tax_code = e.value
  }
  searchTaxCode() {
    Library.showloading();
    if (!this.frmData.buyer_tax_code) {
      Library.notify("Vui lng nhp m s thu!", "error")
    } else {
      this.vatService.getInfoCompany(this.frmData.buyer_tax_code).subscribe(res => {
        if (res.status == "success") {
          this.dataTaxCode = res.data.customer;
          this.frmData.customer_name = this.dataTaxCode.name;
          this.frmData.buyer_address = this.dataTaxCode.business_address;
        }
      })
    }
    setTimeout(() => {
      Library.hideloading();
    }, 1000);
  }


  focusField(tagForm, field) {
    if (tagForm && field) {
      const form_current = this.listDxForm.find(form => form.tabIndex === tagForm);
      if (form_current) {
        form_current.instance.getEditor(field).focus()
      }
    }
  }
}
