import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {IndexComponent} from './components/index/index.component';
import {InvoiceComponent} from './components/invoice/invoice.component';

// import { CreateInvoiceGuard } from '@shared/index';
import {InvoiceResolver, ShowInvoiceResolver} from './shared';
import {VatDeactivateGuard} from '@modules/vat/deactivate/vat-deactivate.guard';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent
  },
  {
    path: 'create',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: InvoiceResolver},
    data: {brec: 'create_invoice'}
  },
  {
    path: ':invoiceId',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ShowInvoiceResolver},
    data: {brec: 'invoice_details'}
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing {
}
