    let tien_chu = Helper.tienBangChu(Math.abs(e.value), this.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);//Helper.money(e.value, currency_code);
    this.frmData.total_payment_in_word = this.invoiceService.docTienAm(e.value, tien_chu, this.configs, this.frmData.currency_code);
