  getDataPreview() {
    let formatter;
    let invoicesPreview = [];
    this.convertGirdToInvoices("preview");

    this.configs.forEach(item => {
      if (item.name == sEnum.configSys.system_parameter) {
        let _obj = JSON.parse(item.value);
        let displayInt = _obj.display_int ? _obj.display_int : false;
        formatter = _obj.decpoint_is_dot ? { decimal: ".", thousands: ",", display_int: displayInt } : { decimal: ",", thousands: ".", display_int: displayInt };
      }

    });

    this.invoicesArr.forEach(data => {
      let decimal_quatity = this.getFormatterToNumber(this.formatter.so_luong);
      let decimal_price = this.getFormatterToNumber(this.formatter.don_gia);
      let decimal_amount = this.getFormatterToNumber(this.formatter.thanh_tien);
      let decimal_amount_vat = this.getFormatterToNumber(this.formatter.tien_thue);
      let decimal_amount_after_vat = this.getFormatterToNumber(this.formatter.tong_tien);
      let total_amount_product_with_vatx = null, total_amount_product_with_vat0 = null, total_amount_product_with_vat5 = null, total_amount_product_with_vat10 = null, amount_vat0 = null;

      data.invoice_products.forEach(item => {
        if (item.commercial_discount) {
          if (item.vat == -1) total_amount_product_with_vatx -= item.amount_after_vat;
          if (item.vat == 0) total_amount_product_with_vat0 -= item.amount_after_vat;
          if (item.vat == 0) amount_vat0 -= item.amount_vat;
          if (item.vat == 5) total_amount_product_with_vat5 -= item.amount_after_vat;
          if (item.vat == 10) total_amount_product_with_vat10 -= item.amount_after_vat;
        } else {
          if (item.vat == -1) total_amount_product_with_vatx += item.amount_after_vat;
          if (item.vat == 0) total_amount_product_with_vat0 += item.amount_after_vat;
          if (item.vat == 0) amount_vat0 += item.amount_vat;
          if (item.vat == 5) total_amount_product_with_vat5 += item.amount_after_vat;
          if (item.vat == 10) total_amount_product_with_vat10 += item.amount_after_vat;
        }
        item['unit_name'] = "";
        this.select.unit_codes.forEach(unit => {
          item.discount = "";
          item.total_discount = "";
          if (unit.code == item.unit_code) {
            item['unit_name'] = unit.name;
          }
        });

        item.amount = Helper.formatMoney(item.amount, decimal_amount, formatter);
        item.price = Helper.formatMoney(item.price, decimal_price, formatter);
        item.quantity = Helper.formatMoney(item.quantity, decimal_quatity, formatter);
      });
      let _obj = this.select.payment_method_names.filter(p => p.code == data.payment_method_name)[0];
      if (_obj) {
        data.payment_method_view_name = _obj.name;
      }
      data.total_amount_product_with_vatx = Helper.formatMoney(total_amount_product_with_vatx, decimal_amount_after_vat, formatter);
      data.total_amount_product_with_vat0 = Helper.formatMoney(total_amount_product_with_vat0, decimal_amount_after_vat, formatter);
      data.total_amount_product_with_vat5 = Helper.formatMoney(total_amount_product_with_vat5, decimal_amount_after_vat, formatter);
      data.total_amount_product_with_vat10 = Helper.formatMoney(total_amount_product_with_vat10, decimal_amount_after_vat, formatter);
      data.amount_after_vatx = Helper.formatMoney(total_amount_product_with_vatx, decimal_amount_after_vat, formatter);
      data.amount_after_vat0 = Helper.formatMoney(total_amount_product_with_vat0, decimal_amount_after_vat, formatter);
      data.amount_after_vat5 = Helper.formatMoney(total_amount_product_with_vat5, decimal_amount_after_vat, formatter);
      data.amount_after_vat10 = Helper.formatMoney(total_amount_product_with_vat10, decimal_amount_after_vat, formatter);
      data.amount_vat0 = amount_vat0;

      data.total_amount_product_vatx = Helper.formatMoney(data.total_amount_product_vatx, decimal_amount, formatter);
      data.total_amount_product_vat0 = Helper.formatMoney(data.total_amount_product_vat0, decimal_amount, formatter);
      data.total_amount_product_vat5 = Helper.formatMoney(data.total_amount_product_vat5, decimal_amount, formatter);
      data.total_amount_product_vat10 = Helper.formatMoney(data.total_amount_product_vat10, decimal_amount, formatter);
      data.amount_vat5 = Helper.formatMoney(data.amount_vat5, decimal_amount_vat, formatter);
      data.amount_vat10 = Helper.formatMoney(data.amount_vat10, decimal_amount_vat, formatter);
      data.amount = Helper.formatMoney(data.amount, decimal_amount, formatter);
      data.total_amount_vat = Helper.formatMoney(data.total_amount_vat, decimal_amount_vat, formatter);
      data.total_payment = Helper.formatMoney(data.total_payment, decimal_amount_after_vat, formatter);

      invoicesPreview.push(data);
    });

    return invoicesPreview;
  }

