import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { Helper, Library } from '@core/index';
import { VatService } from '@modules/vat/shared';

@Component({
  selector: 'app-keep-number',
  templateUrl: './keep-number.component.html',
  styleUrls: ['./keep-number.component.scss']
})
export class KeepNumberComponent implements OnInit {

  invoice_releases: any;
  frmData: any = {};
  soTon: number = 0;

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
  ) { }

  ngOnInit() {
    let invoice_releases = [];
    this.frmData.invoice_date = moment(new Date()).format("YYYY-MM-DD");;
    this.invoice_releases.forEach(item => {
      let soTon = parseInt(item.amount) - parseInt(item.used_amount);
      invoice_releases.push({
        name: `${item.template.template_code} - ${item.template.invoice_series} (${Helper.formatNumberInvoice(item.begin_number)} - ${Helper.formatNumberInvoice(item.end_number)}) - S tn: ${soTon}`,
        value: item.id,
        template_id: item.template_id,
        invoice_type_name: item.invoice_type_name,
        soTon: soTon
      });
    });
    this.invoice_releases = invoice_releases;
  }

  invoiceReleaseChange = (e) => {
    console.log(e.value);
    this.invoice_releases.some(item => {
      if (item.value == e.value) {
        this.frmData.invoice_type_name = item.invoice_type_name;
        this.frmData.template_id = item.template_id;
        this.soTon = item.soTon;
        return true;
      }
    });
  }

  onSubmit(e) {
    let data = { ...this.frmData };
    delete data.invoice_type_name;

    Library.showloading();
    let sub = this.vatService.giuSo(data).subscribe(rs => {
      if (rs.status == "success") {
        Library.notify('Thnh cng', 'success');
        this.bsModalRef.content.successSaveItem = "success";
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {}, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

}
