  ngAfterViewInit() {
    if (this.columns.length > 0) {
      this.columns.forEach(item => {
        this.dataGrid.columns.push(item);
      });
    }
  }
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDonImport().subscribe(message => {
      let data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName == "efy_Sign") {
          if (data.Data.Name == 'hoadon.xml') {
            this.goiApiXuatHoaDon(data.Data.Content);
          } else if (data.Data.Name == 'ten_filek_the_trung-dc-nhi.xml') {
            this.invoice.issuer_name = data.Data.IssuerName;
            this.xuLiApiKhiXuat();
          } else {
            this.invoice.invoice_files.forEach(item => {
              if (item.file_name == data.Data.Name) {
                item.file_content = data.Data.Content;
              }
            });
            this.tempKiFile++;
            if (this.tempKiFile == this.invoice.invoice_files.length) {
              this.xuLiApiKhiXuat();
            }
          }
        }
      } else {
        this.errorImport = true;
        this.showPopupImport = false;
        Library.notify(data.Description, 'error');
      }
    });
  }

  layDuLieuTuHoaDonChoXuat() {
    this.hoaDonImportChoXuat.forEach(invoice => {
      this.invoice_release_id = invoice.invoice_release_id;
      this.template_id = invoice.template_id;
      this.template_code = invoice.template_code;
      this.invoice_series = invoice.invoice_series;
      let number_of_vouchers = invoice.number_of_vouchers ? invoice.number_of_vouchers : Helper.genNumberOfVoucher(this.hoaDonImportChoXuat);
      if(invoice.invoice_products && invoice.invoice_products.length > 0){
        for (let i = 0; i < invoice.invoice_products.length; i++) {
          invoice.invoice_products = invoice.invoice_products.sort((itemA, itemB) => {
            return itemA.order - itemB.order;
          });
          let product = invoice.invoice_products[i];
          this.layDuLieuDong(product, invoice, i, number_of_vouchers);
        }
      }else {
        this.layDuLieuDong({}, invoice, 0, number_of_vouchers);
      }
    });
    this.checkStatusData();
  }


