  readFile() {
    this.isDisReadFile = true;
    Library.showloading();
    const reader: FileReader = new FileReader();

    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      let data = <any>(XLSX.utils.sheet_to_json(ws, { header: "A", range: 0 }));

      let fileBangKe: any = document.getElementById("file-bang-ke");
      fileBangKe.value = "";
      if (this.currentUser.account.tax_code != "0500233676") {
        let _arrData: any = [];
        if (data && data[0]) {
          let columnsName = data.map(e => e.A);
          let _index = columnsName.indexOf("number_of_vouchers")

          if (_index == -1) {
            Library.notify("File mu khng ng nh dng. Vui lng nhn vo Ti y  ly li file mu", "error");
            return false;
          } else {
            if (_index > 0) {
              data.splice(0, _index);
            }
            for (let i = 1; i < data.length; i++) {
              let _row: any = {}
              for (const key in data[i]) {
                if (data[i].hasOwnProperty(key)) {
                  const column = data[0][key]
                  const value = data[i][key];
                  _row[column] = value;
                }
              }
              _arrData.push(_row);
            }
          }
          this.dataExcelFile = _arrData;
          this.fillData(this.dataExcelFile);
        }
      } else {
        this.dataExcelFile = data;
        this.fillDataBravo(this.dataExcelFile);
      }

      //  this.countAll = data.length;
      //  this.showPopupImport = true;
    };
    reader.readAsBinaryString(this.target.files[0]);
  }

