          invoice["customer_name"] = "";
          invoice["buyer_name"] = "";
          invoice["class_name"] = "";
          invoice["student_name"] = "";
          invoice["buyer_address"] = "";
          invoice["buyer_mobile"] = "";
          invoice["buyer_email"] = "";
          invoice["payment_method_name"] = "";
          invoice["payment_method_view_name"] = "";
          invoice["bank_account_number"] = "";
          invoice["bank_name"] = "";
          invoice["note"] = "";
