  checkVat(vat) {
    if (vat == "Khng chu thu" || vat == "khng chu thu") {
      return -1;
    }
    else if (vat == "Khng k khai, tnh np thu GTGT" || vat == "khng k khai, tnh np thu GTGT")
      return -2;
    else if (vat == "Trng hp khc" || vat == "trng hp khc")
      return -3;
    else if (vat == "10% x 70%")
      return 7;
    else if (vat == "5% x 70%")
      return 3.5;
    else {
      return parseInt(vat);
    }
  }
  checkDuplicateNumberOfVouchers(data) {
    if (this.isThemTiep) {
      for (let i = 0; i < data.length; i++) {
        const row = data[i];
        let foundOnDatabase = this.invoicesHaveNumberOfVouchers.filter((item) => {
          return item.number_of_vouchers == row.number_of_vouchers;
        });
        let foundOnGird = this.dataFormGird.filter((item) => {
          return item.number_of_vouchers == row.number_of_vouchers;
        });
        if (foundOnDatabase.length > 0) {
          data[i].statusData = "ERROR_DUPLICATE_DATABASE";
        }
        if (foundOnGird.length > 0 && data[i].number_of_vouchers && data[i].statusData != "ERROR_DUPLICATE_DATABASE") {
          data[i].statusData = "ERROR_DUPLICATE_ONGIRD";
        }
      }
      this.invoice_products = new ArrayStore({
        key: 'keyData',
        data: data
      });
      this.dataSource = new DataSource({
        store: this.invoice_products
      });
    } else {
      for (let i = 0; i < data.length; i++) {
        const row = data[i];
        let foundOnDatabase = this.invoicesHaveNumberOfVouchers.filter((item) => {
          return item.number_of_vouchers == row.number_of_vouchers;
        });
        if (foundOnDatabase.length > 0) {
          data[i].statusData = "ERROR_DUPLICATE_DATABASE";
        }
      }
      this.invoice_products = new ArrayStore({
        key: 'keyData',
        data: data
      });
      this.dataSource = new DataSource({
        store: this.invoice_products
      });
    }
    this.dataFormGird = [];
  }

  checkNumberOfVouchers(numberOfVouchers) {
    let foundNumberOfVouchers;
    this.invoice_products.load().then((dataSource) => {
      foundNumberOfVouchers = dataSource.findIndex((item) => {
        return item.number_of_vouchers == numberOfVouchers;
      });
    });
    return foundNumberOfVouchers;
  }

  checkStatusData() {
    this.invoice_products.load().then((dataSource) => {
      let invalid = dataSource.filter((item) => {
        return item.number_of_vouchers && item.statusData == "INVALID";
      });
      let errorDuplicateDatabase = dataSource.filter((item) => {
        return item.number_of_vouchers && item.statusData == "ERROR_DUPLICATE_DATABASE";
      });
      let errorDuplicateOngird = dataSource.filter((item) => {
        return item.number_of_vouchers && item.statusData == "ERROR_DUPLICATE_ONGIRD";
      });
      let errorData = dataSource.filter((item) => {
        return item.statusData == "DATA_ERROR";
      });
      this.totalInvoiceInvalid = invalid.length;
      this.totalDuplicateInvoice = errorDuplicateDatabase.length;
      this.totalErrorInvoice = errorDuplicateOngird.length + errorData.length;
    });
  }

  formatDecimalColumn(number) {
    if (number == 0) {
      return '0,###';
    } else {
      return '0,###.' + '0'.repeat(number);
    }
  }

  onContextMenuPreparing(e) {
    if (e.row) {
      if (e.row.rowType === "data") {
        e.items = [{
          text: "Thm khch hng",
          onItemClick: () => {
            this.addRow();
          }
        },
        {
          text: "Thm hng ha",
          onItemClick: () => {
            if (e.row.data.number_of_vouchers || e.row.data.productNumberOfVouchers) {
              let temp = e.row.data.number_of_vouchers ? e.row.data.number_of_vouchers : e.row.data.productNumberOfVouchers;
              this.invoice_products.load().then((dataSource) => {
                let products = dataSource.filter((item) => {
                  return item.number_of_vouchers == temp || item.productNumberOfVouchers == temp;
                });
                let data = this.frmData.addImportRow(products.length + 1, products.length + 1);
                data.dataType = "HANG_HOA";
                if (e.row.data.number_of_vouchers) {
                  data["productNumberOfVouchers"] = e.row.data.number_of_vouchers;
                  dataSource.splice(e.rowIndex + products.length, 0, data);
                } else {
                  data["productNumberOfVouchers"] = e.row.data.productNumberOfVouchers;
                  let index = dataSource.map(function (e) { return e.number_of_vouchers; }).indexOf(e.row.data.number_of_vouchers);
                  dataSource.splice(index + products.length - 1, 0, data);
                }
                this.invoice_products.clear();
                dataSource.forEach(row => {
                  this.invoice_products.insert(row);
                })
              });
              this.dataGrid.instance.refresh();
            } else {
              Library.notify("Bn cha nhp s phiu / chng t", "error");
            }
          }
        }];
      }
    }
  }
  onInvoiceReleases(e) {
    let selectedItem = e.component.option("selectedItem");
    this.template_id = selectedItem.template_id;
    this.invoice_release_id = e.value;
    this.is_multi_vat = selectedItem.is_multi_vat;
    if (VStringService._getValueToString(selectedItem.template_type) == sEnum.templatetype.HoaDonBatDongSan)
      this.isBS = true;
    else
      this.isBS = false;
    //Load li danh sch cu hnh cc trng
    if (this.configs.config_column_import_inv_excel && this.configs.config_column_import_inv_excel.length > 0) {
      this.listColumn = Helper.listColumnImportExcel(this._translate);
      this.listColumn.forEach(item => {
        let _obj = this.configs.config_column_import_inv_excel.filter(p => p.column_code == item.column_code)[0];
        if (_obj) {
          item.is_visible = _obj.is_visible;
        }
      });
    } else {
      this.listColumn = Helper.listColumnImportExcel(this._translate);
    }
    if (this.columns.length > 0) {
      this.columns.forEach(item => {
        this.dataGrid.columns.forEach((column, index) => {
          if (column["dataField"] == item.dataField)
            this.dataGrid.columns.splice(index, 1);
        });
      });
    }

    this.columns = [];
    this.invoiceOptions = [];

    if (selectedItem.fields && selectedItem.fields.length > 0) {
      const _dulieumorong = selectedItem.fields.filter(x => x.type === '4');
      if (_dulieumorong.length > 0) {
        _dulieumorong.forEach(item => {
          if (item.is_show) {
            let oField = {
              column_code: item.code,
              column_name: item.name,
              is_visible: true
            }
            this.listColumn.push(oField);
            let column =
            {
              caption: item.name,
              dataField: item.code,
              width: 150
            };
            this.columns.push(column);
            let invoiceOption =
            {
              code: item.code,
              name: item.name,
              value: '',
              data_type: item.data_type
            }
            this.invoiceOptions.push(invoiceOption);
          }
        });
        if (this.columns.length > 0) {
          this.columns.forEach(item => {
            this.dataGrid.columns.push(item);
          });
        }
      }
    }
  }

