  onCellPrepared(e) {
    if (e.rowType == "data") {
      if (e.data.dataType && e.data.dataType == "HANG_HOA" && this.fieldDisable.includes(e.column.dataField)) {
        e.cellElement.style.background = "#F1F1F1";
      }
      if (e.data.statusData == "ERROR_DUPLICATE_ONGIRD" && e.column.dataField == "number_of_vouchers") {
        e.cellElement.style.background = "#FF0000";
      }
      if (e.data.number_of_vouchers) {
        switch (e.column.dataField) {
          case "buyer_address":
            if (e.data.customer_object_code == false) this.setStatusDataOnCellPrepared(e, "a ch khng c  trng");
            break;
          case "payment_method_name":
            this.setStatusDataOnCellPrepared(e, "Hnh thc thanh ton khng c  trng");
            break;
          case "template_code":
            this.setStatusDataOnCellPrepared(e, "Mu s ha n khng c  trng");
            break;
          case "invoice_series":
            this.setStatusDataOnCellPrepared(e, "K hiu ha n khng c  trng");
            break;
          case "product_name":
            this.setStatusDataOnCellPrepared(e, "Tn hng ha khng c  trng");
            break;
          case "amount":
            this.setStatusDataOnCellPrepared(e, "Thnh tin khng c  trng");
            break;
          case "amount_after_vat":
            this.setStatusDataOnCellPrepared(e, "Tng tin khng c  trng");
            break;
          case "customer_name":
            if (e.data.customer_object_code == false) this.setStatusDataOnCellPrepared(e, "Tn n v t chc khng c  trng");
            break;
          case "buyer_name":
            if (e.data.customer_object_code == true) this.setStatusDataOnCellPrepared(e, "Tn khch hng khng c  trng");
            break;
          case "product_code":
            this.setStatusDataOnCellPrepared(e, this._translate.VALIDATION.required_productCode);
            break;
          case "passport_expiration_date":
            if (this.isToKhaiThue) {
              let passport_date = new Date(e.data.passport_date)
              if (!e.value) {
                this.setStatusDataOnCellPrepared(e, "Ngy ht hn h chiu khng c  trng");
              }
              if (e.value && passport_date >= e.value) {
                this.setStatusDataOnCellPrepared(e, "Ngy cp h chiu khng c ln hn ngy ht hn h chiu");
              }
            }
            break;
          case "passport_date":
            if (this.isToKhaiThue) {
              let passport_expiration_date = new Date(e.data.passport_expiration_date)
              if (!e.value) {
                this.setStatusDataOnCellPrepared(e, "Ngy cp h chiu khng c  trng");
              }
              if (e.value && e.value >= passport_expiration_date) {
                this.setStatusDataOnCellPrepared(e, "Ngy cp h chiu khng c ln hn ngy ht hn h chiu");
              }
            }
            break;
          case "passport":
            if (this.isToKhaiThue) {
              if (!e.value) {
                this.setStatusDataOnCellPrepared(e, "H chiu khng c  trng");
              }
            }
            break;
          case "nationality":
            if (this.isToKhaiThue) {
              if (!e.value) {
                this.setStatusDataOnCellPrepared(e, "Quc tch khng c  trng");
              }
            }
            break;
          default:
            break;
        }
      } else if (e.data.number_of_vouchers == "" && e.data.dataType == "KHACH_HANG" && e.data.product_name && e.column.dataField == "number_of_vouchers") {
        this.setStatusDataOnCellPrepared(e, "S phiu, chng t khng c  trng");
      }
    }
  }

  setStatusDataOnCellPrepared(e, message) {
    if (((e.column.dataField == "amount" || e.column.dataField == "amount_after_vat") && e.value == null && !e.data.is_promotion) || ((e.column.dataField != "amount" && e.column.dataField != "amount_after_vat" && e.column.dataField != "product_code") && (e.value == null || e.value == "")) || (e.column.dataField == "product_code" && VStringService._getValueToString(e.value) != "" && !this.productCodePattern.test(e.value)) || (this.isToKhaiThue && (e.column.dataField == "passport_date" || e.column.dataField == "passport_expiration_date"))) {
      e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
      e.cellElement.title = message;
      if (e.data.statusData != "ERROR_DUPLICATE_DATABASE" && e.data.statusData != "ERROR_DUPLICATE_ONGIRD") e.data.statusData = "DATA_ERROR";
    }
    else {
      if (e.data.customer_object_code == false && (e.data.buyer_address == null || e.data.buyer_address == "" || e.data.customer_name == null || e.data.customer_name == ""))
        e.data.statusData == "DATA_ERROR";
      else
        if (VStringService._getValueToString(e.data.product_code) != "" && !this.productCodePattern.test(e.data.product_code))
          e.data.statusData == "DATA_ERROR";
        else
          if (e.data.customer_object_code == true && (e.data.buyer_name == null || e.data.buyer_name == ""))
            e.data.statusData == "DATA_ERROR";
          else
            if (e.data.payment_method_name == null || e.data.payment_method_name == "")
              e.data.statusData == "DATA_ERROR";
            else
              if (e.data.product_name == null || e.data.product_name == "")
                e.data.statusData == "DATA_ERROR";
              else
                if (e.data.amount == null)
                  e.data.statusData == "DATA_ERROR";
                else if (e.key.statusData != "ERROR_DUPLICATE_DATABASE") {
                  let passport_expiration_date = new Date(e.data.passport_expiration_date)
                  let passport_date = new Date(e.data.passport_date)
                  if (this.isToKhaiThue && (e.data.passport_expiration_date == "" || e.data.passport_date == "" || e.data.nationality == "" || e.data.passport == "" || passport_date >= passport_expiration_date)) {
                    e.data.statusData == "DATA_ERROR";
                  } else {
                    e.data.statusData = "INVALID"
                  }
                }
                else
                  if (e.data.amount_after_vat == null)
                    e.data.statusData == "DATA_ERROR";
                  else if (e.data.number_of_vouchers == "" && e.data.dataType == "KHACH_HANG" && e.data.product_name && e.column.dataField == "number_of_vouchers")
                    e.data.statusData == "DATA_ERROR";
                  else
                    e.data.statusData = "INVALID"
    }
  }
