  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems.filter(p => p.status === 'DA_XUAT');
      if (_lst.length > 0 || data) {
        // if (_lst.length > 10) {
        //   Library.notify(this._translate.NOTIFY.only_up_to_100_invoices_can_be_selected_for_viewing, 'error');
        //   return false;
        // }
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          let data = this.vatService.taodulieuXemMau(ids);
          data['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_hoa_don
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  inChuyenDoi(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.selectedItems.length || data) {
      const _lst = this.selectedItems.filter(p => p.status == 'DA_XUAT');
      if (_lst.length > 0 || data) {
        let ids = [];
        if (data) {
          if (data.status === 'DA_XUAT') {
            ids.push(data.id);
          } else {
            Library.notify(this._translate.NOTIFY.not_print_convert, 'error');
            return;
          }
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          const data = this.vatService.taodulieuInMau(ids, false);
          data['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          // Xem nhiu trang 
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.in_chuyen_doi_hd
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }

    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

